* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 22px;
  background: #121111;
  color: lightgray;
}

body {
  min-height: 100vh;
  font-family: 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  border: 1px solid #040404;
  margin: auto;
}

header {
  width: 100%;
  padding: 12px;
  background-color: #040404;
  color: #ef9f45;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: Roboto, 'Helvetica Neue', sans-serif;
}

main {
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  overflow-y: auto;

}

footer {
  width: 100%;
  padding: 0.25em;
  background-color: #1e1e1e;
  color: aliceblue;
  display: grid;
  place-content: center;
  font-family: Roboto, 'Helvetica Neue', sans-serif;
  font-size: small;
}



code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

textarea {
  font-family: 'Fira Code', monospace;
  font-size: larger;
  resize: none;
  width: 100%;
  height: 100%;
  border: none;
  background-color: transparent;
  color: #d2d2d2;
}

textarea:focus {
  outline: none;
}

textarea::selection {
  color: #040404;
  background-color: #ea991f;
}
